.cards {
    padding: 1rem 1rem 1rem 1rem;
    font-size: 1.5rem;
    display: flex;
    flex-wrap: wrap;
}

.clipDivs{
    display:flex;
    flex-direction: row;
}

.tableTitle{
    text-align: center;
}

td {
    font-size: 1.25rem;
}

.animDisclaim{
    font-size: .5rem;
}

h3{
    padding: 0 1rem 0 1rem
}
@media only screen and (max-width: 600px) {
    .cards {
      font-size: 1rem;
      padding: .25rem .25rem .25rem .25rem;
    }

    .clipDivs{
        display:flex;
        flex-direction: column;
    }
    td{
        font-size: .85rem;
    }
  }